import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import SojiBaseInit from '@modules/SojiBase';
import Structure from '../../Structure';
import Header from './components/Header';
import ReviewCard from './components/ReviewCard';
import ReviewFeed from './components/ReviewFeed';
import AboutCompany from './components/AboutCompany';
import styled from 'styled-components';

import Preloader from '../../../components/preloader';

const Container = styled.div`
    height: 100vh;
    width: 100vw;
`;

export default function CompanyProfile() {
    const [fetchedData, setfetchedData] = useState(false);
    const { company_name } = useParams();

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get(`https://companyapi.sojiare.com/v1/review/company/${company_name}`, true);
            getter.on('static', (response) => {
                if (response.response) {
                    if (response.response.data) {
                        setfetchedData(response.response.data);
                    } else {
                        setfetchedData([]);
                    }
                }
            });
        })();
    }, []);

    console.log(fetchedData)

    return (
        <Container>
            {!fetchedData ? (
                <Preloader />
            ) : (
                <>
                    {fetchedData.id ? (
                        <Structure fullpage>
                            <Header data={fetchedData} />
                            <div className="container">
                                <div className="row">
                                    <div className="col-md-7">
                                        <ReviewCard data={fetchedData} />
                                        {fetchedData.companyReviews.map((data, index) => {
                                            return <ReviewFeed data={data} key={index} />;
                                        })}
                                    </div>
                                    <div className="col-md-5 order-first order-md-last">
                                        <AboutCompany data={fetchedData} />
                                    </div>
                                </div>
                            </div>
                        </Structure>
                    ) : (
                        <Structure>
                            <p>No Data Found for that ({company_name})</p>
                        </Structure>
                    )}
                </>
            )}
        </Container>
    );
}
